import React, { useState } from 'react';
import './App.scss';
import "aos/dist/aos.css";
import menu from './assets/img/burgermenu.svg';
import Main from './Main';
import Works from './works';
import Aboutme from './Aboutme';
import { BrowserRouter, NavLink, Redirect, Route } from 'react-router-dom';
import Cases from './Cases';
import works from './assets/img/works.png';


function App() {

  const [menuToggler, setMenuToggler] = useState(false);

  let [zxc, setzxc] = useState(false);

  return (
    <BrowserRouter>
    {!zxc && <Redirect to={'/main'}/>}
    <div className="App" id='App'>
        <div className="menu image">
          <img src={menu}  onClick={()=> {setMenuToggler(!menuToggler)}}/>
        </div>
        {menuToggler&&<div className="menu-block">
          <div className="menu-wrapper">
            <div className="menu-item">
              <NavLink to="/main">ГЛАВНАЯ</NavLink>
              </div>
            <div className="menu-item">
              <NavLink to="/aboutme">ОБО МНЕ</NavLink>
              </div>
            <div className="menu-item">
              <NavLink to="/works">РАБОТЫ</NavLink>
            </div>
            <div className="menu-item">
              <NavLink to="/cases">КЕЙСЫ</NavLink>
              </div>
          </div>
        </div>}
        <Route path='/main' component={Main}/>
        <Route path='/aboutme' component={Aboutme}/>
        <Route path='/cases' component={Cases}/>
        <Route path='/works' component={Works}/>
    </div>
    </BrowserRouter>
  );
}

export default App;
