import React, { useEffect, useState } from 'react';
import './Aboutme.scss';
import photo from './assets/img/aboutme_photo.png';




function Aboutme() {

  return (
    <div className="Aboutme">
        <div className="Aboutme_content">
            <div className="Aboutme_text">
            «Умение хорошо одеваться не зависит от большого количества одежды. Это вопрос гармонии и здравого смысла». 
            <br/> <br/> Именно эти слова Oscar de la Renta полностью характеризую мою работу. 
            <br/> <br/> Насмотренность - одно из самых важных качеств которым должен обладать каждый стилист. Именно этот фактор является моим преимуществом перед другими моими коллегами. Последние десять лет я жил в Италии и работал в сфере моды, сотрудничая с международными брендами, научился чувствовать и интегрировать все новые тенденции, когда они только зарождаются. 
            <br/> <br/>Очень важно не путать «модно» и «стильно», это совсем разные понятия. В переводе с латыни мода - временное, непостоянное преобладание вкусов или постоянное изменение. Стиль в свою очередь более постоянное явление и определяется вещами, которые подбираются и характеризуют конкретного человека. Мне нравится наблюдать за тем, как одеваются люди, особенно, в обычные дни. Именно тогда проявляется стиль, а одеться нарядно может каждый. 
            </div>
            <div className="Aboutme_photo image">
              <img src={photo}/>
            </div>
    </div>
    </div>
  );
}

export default Aboutme;
